    .van-contact-card--add .van-contact-card__value {
      display: none
    }

    .van-contact-card {
      padding: 2rem;
      box-sizing: border-box;
    }

    .o_payment {
      background: #E93323;
      padding: 0.5rem 1rem;

      &_one {
        height: 2.6rem;
        // width: 2.6rem;
        border-radius: 50%;

        &_img {
          height: 2.6rem;
          width: 2.6rem;
          border-radius: 50%;
        }
      }
    }

    .van-step--finish .van-step__circle,
    .van-step--finish .van-step__line {
      background-color: #E93323
    }

    .van-steps--horizontal {
      padding: 0 1rem;
    }

    .c_bg_font {
      background: #FFFFFF;
      color: #AAAAAA;
      border: 1px solid #AAAAAA;
    }
  }
</style>



